.HomePage {
  /* background-color: rgb(74, 56, 234); */ /* Couleur de fond retirée pour voir la vidéo */
  position: relative; /* Nécessaire pour positionner la vidéo absolument à l'intérieur */
  overflow: hidden; /* Cache les parties de la vidéo qui pourraient déborder */
  min-height: 100vh; /* Prend au moins toute la hauteur de la fenêtre */
  width: 100%; /* Prend toute la largeur disponible */
  display: flex;
  flex-direction: column;
  align-items: center; /* Centre les enfants horizontalement */
  justify-content: flex-start; /* Aligne les enfants (HomePageContent) en haut */
}

.backgroundVideo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* S'assure que la vidéo couvre l'espace sans être déformée */
  z-index: -1; /* Place la vidéo derrière tout autre contenu */
  /* Optionnel: pour améliorer la lisibilité du texte par-dessus la vidéo */
  /* filter: brightness(0.7); */
}

.videoOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #19280199; /* Filtre noir semi-transparent. Ajustez la couleur et l'opacité (0.3 ici) */
  z-index: 0; /* Se place entre la vidéo (z-index: -1) et le contenu (z-index: 1) */
}
.BackgroundFoto img {
  /* object-fit: cover; */
  position: absolute; /* Positionne l'image par rapport à .HomePage */
  top: 0; /* Aligne en haut */
  left: 0; /* Aligne à gauche */
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  /* filter: brightness(0.9); */
}
.HomePageContent {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centre le <p> (et autres futurs éléments) horizontalement dans ce bloc */
  justify-content: center; /* Centre le <p> verticalement dans ce bloc. Si vous voulez que le <p> soit en haut de ce bloc, changez pour flex-start */
  margin-top: 20px; /* Crée un espace de 10px au-dessus de ce bloc */
  /* text-align: center; /* Décommentez si vous voulez que le texte du paragraphe soit centré */
  position: relative; /* S'assurer que le contenu est au-dessus de la vidéo */
  z-index: 1; /* S'assurer que le contenu est au-dessus de la vidéo */
}

.HomePageContentA p {
  font-size: 35px;
  /* color: rgb(121, 210, 240); */
  color: whitesmoke;
  /* font-family: "source-sans-pro.regular", Arial, sans-serif; */
  font-family: "Poppins-Medium", Arial, sans-serif;

  margin-top: 30px;
  position: relative; /* S'assurer que le contenu est au-dessus de la vidéo */
  z-index: 1; /* S'assurer que le contenu est au-dessus de la vidéo */
}
.Logo {
  position: relative; /* S'assurer que le contenu est au-dessus de la vidéo */
  z-index: 1; /* S'assurer que le contenu est au-dessus de la vidéo */
}
.Logo img {
  display: block;
  width: 60%;
  height: auto;
  visibility: visible;
  opacity: 1;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  margin-bottom: 20px;
  border-radius: 2px;
  box-shadow: 5px 5px 10px #19280199;
}

.HomePageContentB {
  position: relative; /* S'assurer que le contenu est au-dessus de la vidéo */
  z-index: 1; /* S'assurer que le contenu est au-dessus de la vidéo */
}

.HomePageContentB p {
  font-size: 20px;
  color: whitesmoke;
  /* color: rgb(121, 194, 218); */
  /* font-family: "source-sans-pro.regular", Arial, sans-serif; */
  /* font-family: "JosefinSans-Thin", Arial, sans-serif; */
  font-family: "Poppins-Medium", Arial, sans-serif;
  /* text-align: center; Centre le texte */
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  margin-left: 10%;
  margin-right: 10%;
  padding: 1em 1.5em; /* Ajoute de l'espace autour du texte */
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
  background-color: rgba(
    0,
    0,
    0,
    0.55
  ); /* Fond noir semi-transparent, cohérent avec HomePageContentA p */
  border-radius: 2px; /* Coins arrondis */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /* Optionnel: pour améliorer la lisibilité */
}

@media (max-width: 1224px) {
  .HomePageContentA p {
    font-size: 30px;
  }
  .HomePageContentB p {
    font-size: 16px;
  }
}
@media (max-width: 509px) {
  .HomePageContentA p {
    font-size: 25px;
  }
  .HomePageContentB p {
    font-size: 12px;
  }
}
